.bar {
    display: inline-block;
    fill: green;
}

.tick {
    z-index: 1000;
}

.bar:hover {
    fill: orangered;
}

svg {
    background-color: white;
    display: block;
    z-index: 23;
}

.chart-title {
    font-weight: bold;
    font-size: 36px;
    font-family: sans-serif;
}

#tooltip {
    position: absolute;
    width: auto;
    padding: 10px;
    background-color: white;
    font-weight: bold;
    font-family: sans-serif;
    border: 1px solid black;
}
